import './Menu.scss';
import manuIcon from './../../assets/images/menu.svg';
// import userAvatar from './../../assets/images/user.jpg';
import userAvatar from '../../assets/images/user.png';
import Divider from '@mui/material/Divider';
import { NavLink } from 'react-router-dom';

const Menu = (props) => {
    return (
        <nav className='menu'>
            <a className='menu__avatar' href="#"><img src={userAvatar} alt=""/></a>
            
            {/* <button><img src={manuIcon} width={32} alt="" /></button> */}
            <ul>
                <NavLink className='menu__link' to={'/home'}>Home</NavLink>
                <NavLink className='menu__link' to={'/stats'}>Stats</NavLink>
                <NavLink className='menu__link' to={'/settings'}>Settings</NavLink>
            </ul>

        </nav>
    )
}

export default Menu;