import './Settings.scss';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import wallpaper1 from '../../assets/images/wallpaper1.jpg';
import wallpaper2 from '../../assets/images/wallpaper2.jpg';
import wallpaper3 from '../../assets/images/wallpaper3.jpg';
import wallpaper4 from '../../assets/images/wallpaper4.jpg';
import { connect } from 'react-redux';
import { changeBG } from '../../redux/main-reducer';

const Settings = ({changeBG}) => {
    return (
        <div className='settings'>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>Background</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        You can choose background for your ToDo list.
                    </Typography>
                    <div className='settings__bgRow'>
                        <div className='settings__bgCol' onClick={() => changeBG(1)}>
                            <img src={wallpaper1} alt="" />
                        </div>
                        <div className='settings__bgCol'>
                            <img src={wallpaper2} alt="" onClick={() => changeBG(2)}/>
                        </div>
                        <div className='settings__bgCol'>
                            <img src={wallpaper3} alt="" onClick={() => changeBG(3)}/>
                        </div>
                        <div className='settings__bgCol'>
                            <img src={wallpaper4} alt="" onClick={() => changeBG(4)}/>
                        </div>

                    </div>
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>Accordion 2</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <Typography>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse
                        malesuada lacus ex, sit amet blandit leo lobortis eget.
                    </Typography>
                </AccordionDetails>
            </Accordion>
        </div>
    )
}

export default connect(null, {
    changeBG
})(Settings);