import { connect } from 'react-redux';
import './TasksList.scss';
import { addTask, setTask } from '../../redux/main-reducer';
import TaskItem from './../TaskItem/TaskItem';
// import addIcon from './../../assets/images/add.svg';
import EmptyList from '../EmptyList/EmptyList';
import GreetingsBlock from '../GreetingsBlock/GreetingsBlock';
import AddIcon from '@mui/icons-material/Add';
import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';
import AddCircleIcon from '@mui/icons-material/AddCircle';


const Main = ({
    addTask,
    setTask,
    currentNewTask,
    tasks,
}) => {



    const onchangedCurrentTask = (e) => {
        setTask(e.currentTarget.value)
    }

    const onclickAddTask = () => {
        console.log(tasks)
        addTask(currentNewTask)
    }

    return (
        <div className='main'>
            <GreetingsBlock />

            <div className='main__newTask'>
                <input className='main__input' 
                value={currentNewTask} 
                onChange={onchangedCurrentTask} 
                placeholder="Enter ToDo..." 
                maxLength={32}/>
                {/* <TextField className='main__input' id="filled-basic" label="Filled" variant="filled" value={currentNewTask} onChange={onchangedCurrentTask} placeholder="Enter ToDo..."/> */}
                {/* <button onClick={onclickAddTask}><AddIcon/></button> */}
                <div>
                <IconButton onClick={onclickAddTask}>
                    <AddCircleIcon color='primary' fontSize='large'/>
                </IconButton>
                </div>
                

            </div>
            <div className='main__row'>
                <div className='main__col'>
                    <h2>Tasks</h2>
                    {tasks.map((task, index) => {
                        if (!task.check) {
                            return <TaskItem key={index}
                                id={task.taskID}
                                task={task.taskText}
                                check={task.check} />
                        }
                    })}
                    <EmptyList tasks={tasks}/>
                </div>
                <div className='main__col'>
                    <h2>Complete tasks</h2>
                    {tasks.map((task, index) => {
                        if (task.check) {
                            return <TaskItem key={index}
                                id={task.taskID}
                                task={task.taskText}
                                check={task.check} />
                        }
                    })}
                    <EmptyList tasks={tasks}/>
                </div>
            </div>


        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        currentNewTask: state.main.currentNewTask,
        tasks: state.main.tasks,
    }
}

export default connect(mapStateToProps, {
    addTask,
    setTask
})(Main);