import './GreetingsBlock.scss';

const GreetingsBlock = (props) => {

    let date = new Date();
    let weekDay = date.getDay();
    let day = date.getDate();
    let month = date.getMonth();
    let year = date.getFullYear();


    switch(day) {
        case 0:
            weekDay = 'Sunday';
            break;
        case 1:
            weekDay = 'Monday';
            break;
        case 2:
            weekDay = 'Tuesday';
            break;
        case 3:
            weekDay = 'Wednesday';
            break;
        case 4:
            weekDay = 'Thursday';
            break;
        case 5:
            weekDay = 'Friday';
            break;
        case 6:
            weekDay = 'Saturday';
            break;
        default:
            weekDay = ''
            break;
    }

    return (
        <div className="greetingsBlock">
            <h1>Hello, User!</h1>
            <div className='greetingsBlock__weekDay'>{weekDay}</div>
            <div className='greetingsBlock__date'>{day}/{month+1}/{year}</div>
        </div>
    )
}

export default GreetingsBlock;