import './Stats.scss';
import { connect } from 'react-redux';

const Stats = ({tasks}) => {

    let tasksCount = 0;
    let completeTasksCount = 0;
    for (let i = 0; i < tasks.length; i++) {
        if (!tasks[i].check) {
            tasksCount++;
        } else {
            completeTasksCount++;
        }
    }

    return (
        <div className='stats'>
            <div>Current tasks {tasksCount}</div>
            <div>Complete tasks {completeTasksCount}</div>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        tasks: state.main.tasks
    }
}

export default connect(mapStateToProps, {

})(Stats);